
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import {pay} from '../config'
export default {
  name: 'pay',
  head: {
    title: {
      inner: '支付'
    }
  },
  components: {
    Group,
    Cell,
    XButton,
    Radio,
    Loading
  },
  data () {
    return {
      paying: true,
      info: {},
      userId: this.$route.params.userId,
      orderId: this.$route.params.orderId,
      loading: false,
      paymodel: 2,
      // pay: [{
      //   icon: 'static/img/alipay.png',
      //   key: 1,
      //   value: '支付宝'
      // }, {
      //   icon: 'static/img/wechat.png',
      //   key: 2,
      //   value: '微信'
      // }]
      wx: this.$wechat,
      pay: [{
        icon: 'static/img/wechat.png',
        key: 2,
        value: '微信'
      }]
    }
  },
  created () {
    this.info = JSON.parse(this.$localStorage.get('orderDetail'))
    console.log(this.info)
  },
  methods: {
    handlePay () {
      this.$vux.loading.show({
        text: '支付中！'
      })
      this.loading = true
      setTimeout(() => {
        this.loading = false
        this.$vux.loading.hide()
        console.log(pay)
        this.$http({
          method: 'jsonp',
          url: pay,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: {
            userId: this.userId,
            orderId: this.orderId,
            payType: this.paymodel
          }
        })
        .then(res => {
          this.loading = false
          if (res.body.status) {
            this.$vux.toast.show({
              type: 'text',
              width: '22em',
              position: 'bottom',
              text: '恭喜，支付成功！',
              time: '3000'
            })
            setTimeout(() => {
              this.$router.replace('/paysuccess/' + this.userId + '/' + this.orderId)
            }, 1000)
          } else {
            this.$vux.toast.show({
              type: 'text',
              width: '22em',
              position: 'bottom',
              text: res.body.msg,
              time: '3000'
            })
          }
        })
      }, 2000)
    }
  }
}



